import { bookingRoomApi, getRoomListApi } from "@/apis/rooms";
import { getRoomDetailApi } from "@/apis/room-detail";
import { getRateApi } from "@/apis/rate";
const state = () => {
  return {
    roomList: [],
    roomDetail: {},
    rateList: [],
  };
};
const mutations = {
  setRoomListMutations(state, payload) {
    state.roomList = payload;
  },
  setRoomDetailMutations(state, payload) {
    state.roomDetail = payload;
  },

  setRateMutations(state, payload) {
    state.rateList = payload;
  },
};
const actions = {
  async getRoomListActions(context, payload) {
    const data = await getRoomListApi(payload);
    context.commit("setRoomListMutations", data);
  },

  async getRoomDetailActions(context, payload) {
    const data = await getRoomDetailApi(payload);
    context.commit("setRoomDetailMutations", data);
  },

  async getRateActions(context, payload) {
    const data = await getRateApi(payload);
    context.commit("setRateMutations", data);
  },

  async bookingRoomActions(context, payload) {
    const data = await bookingRoomApi(payload);
    console.log(data);
    console.log(context);
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
