<template>
  <div
    class="mfp-wrap mfp-close-btn-in mfp-auto-cursor my-mfp-zoom-in mfp-ready my-bg"
    tabindex="-1"
    style="
      background-image: url('https://media.cntraveler.com/photos/5d112d50c4d7bd806dbc00a4/3:2/w_2250,h_1500,c_limit/airbnb%20luxe.jpg');
    "
  >
    <div class="mfp-container mfp-inline-holder">
      <div class="mfp-content">
        <div id="sign-in-dialog" class="zoom-anim-dialog">
          <div class="small-dialog-header">
            <h3>Sign Up</h3>
          </div>

          <!--Tabs -->
          <div class="sign-in-form style-1">
            <div class="tabs-container alt">
              <!-- Register -->
              <div class="tab-content" id="tab2">
                <form
                  method="post"
                  class="register"
                  @submit.prevent="handleSubmitRegister"
                >
                  <p class="form-row form-row-wide">
                    <label for="username2"
                      >Username:
                      <i class="im im-icon-Male"></i>
                      <input
                        type="text"
                        class="input-text"
                        name="username"
                        id="username2"
                        v-model="userRegister.name"
                        required
                      />
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="email2"
                      >Email Address:
                      <i class="im im-icon-Mail"></i>
                      <input
                        type="text"
                        class="input-text"
                        name="email"
                        id="email2"
                        v-model="userRegister.email"
                        required
                      />
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="phone"
                      >Phone:
                      <i class="im im-icon-Smartphone-3"></i>
                      <input
                        type="text"
                        class="input-text"
                        name="phone"
                        id="phone"
                        v-model="userRegister.phone"
                        required
                      />
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="birthday"
                      >Birthday:
                      <div style="display: flex">
                        <select
                          style="margin-right: 5px"
                          v-model="userRegister.year"
                          required
                        >
                          <option label="year"></option>
                          <option v-for="(year, index) in years" :key="index">
                            {{ year }}
                          </option>
                        </select>
                        <select
                          style="margin-right: 5px"
                          v-model="userRegister.month"
                          required
                        >
                          <option label="month"></option>
                          <option v-for="(month, index) in months" :key="index">
                            {{ month }}
                          </option>
                        </select>
                        <select v-model="userRegister.day" required>
                          <option label="day"></option>
                          <option v-for="(day, index) in days" :key="index">
                            {{ day }}
                          </option>
                        </select>
                      </div>
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="gender"
                      >Gender:
                      <select v-model="userRegister.gender" required>
                        <option value="true">Male</option>
                        <option value="false">Female</option>
                      </select>
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="password1"
                      >Password:
                      <i class="im im-icon-Lock-2"></i>
                      <input
                        class="input-text"
                        type="password"
                        name="password1"
                        id="password1"
                        required
                        v-model="userRegister.password"
                      />
                    </label>
                  </p>

                  <p class="form-row form-row-wide">
                    <label for="password2"
                      >Repeat Password:
                      <i class="im im-icon-Lock-2"></i>
                      <input
                        class="input-text"
                        type="password"
                        name="password2"
                        id="password2"
                        v-model="userRegister.repeatPassword"
                      />
                    </label>
                  </p>

                  <input
                    type="submit"
                    class="button border fw margin-top-10"
                    name="register"
                    value="Register"
                  />
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { createArrayNumber } from "@/utils/createArrayNumber";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { reactive } from "vue";
export default {
  setup() {
    const store = useStore();
    const router = useRouter();
    const years = createArrayNumber(1960, 2023);
    const months = createArrayNumber(1, 12);
    const days = createArrayNumber(1, 31);

    const userRegister = reactive({
      name: "",
      email: "",
      password: "",
      repeatPassword: "",
      phone: "",
      year: "",
      month: "",
      day: "",
      gender: true,
      address: "",
    });

    const handleSubmitRegister = () => {
      if (userRegister.password === userRegister.repeatPassword) {
        const data = {
          name: userRegister.name,
          email: userRegister.email,
          password: userRegister.password,
          phone: userRegister.phone,
          birthday: `${userRegister.year}/ ${userRegister.month}/ ${userRegister.day}`,
          gender: userRegister.gender === "true",
          address: userRegister.address,
        };
        store.dispatch("auth/signUpActions", { data, router });
      } else {
        alert("Máº­t kháº©u khÃ´ng trÃ¹ng khá»p");
      }
    };

    return {
      years,
      months,
      days,
      userRegister,
      handleSubmitRegister,
    };
  },
};
</script>
  
  <style lang="scss">
.my-bg {
  top: 0px;
  position: absolute;
  height: 100vh;
  overflow: scroll;
}
</style>