<template>
  <!-- Listing Item -->
  <div class="col-lg-12 col-md-12">
    <div
      class="listing-item-container list-layout"
      data-marker-id="1"
      @click="handleCLickRoom(room)"
    >
      <a class="listing-item">
        <!-- Image -->

        <div class="listing-item-image">
          <img :src="room.image" alt="" />
          <span class="tag">Eat & Drink</span>
        </div>

        <!-- Content -->
        <div class="listing-item-content">
          <div class="listing-badge now-open">Now Open</div>

          <div class="listing-item-inner">
            <h3>{{ room.name }} <i class="verified-icon"></i></h3>
            <span> {{ room.locationId.name }} </span>
            <div class="star-rating" data-rating="3.5">
              <div class="rating-counter">Price: {{ room.price }}</div>
            </div>
          </div>

          <span class="like-icon"></span>
        </div>
      </a>
    </div>
  </div>
  <!-- Listing Item / End -->
</template>

<script>
import { useRouter } from "vue-router";
export default {
  props: {
    room: {
      type: Object,
    },
  },
  setup(props) {
    const router = useRouter();
    const handleCLickRoom = () => {
      router.push(`/room-detail/${props.room._id}`);
    };
    return {
      handleCLickRoom,
    };
  },
};
</script>

