import { getLocationListApi } from "@/apis/locationApi";

const state = () => {
  return {
    locationList: [],
  };
};
const mutations = {
  setLocationListMutations(state, payload) {
    state.locationList = payload;
  },
};
const actions = {
  async getLocationListActions(context, payload) {
    const data = await getLocationListApi(payload);
    context.commit("setLocationListMutations", data);
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
