import { getListTicketByUserIdApi } from "@/apis/user";

const state = () => {
  return {
    listTicket: [],
  };
};

const mutations = {
  setListTicketByUserMutation(state, payload) {
    const data = payload.map((item) => ({
      _id: item.id,
      checkIn: item.checkIn,
      checkOut: item.checkOut,
      ...item.roomId,
    }));
    state.listTicket = data;
  },
};
const actions = {
  async getListTicketByUserAction({ commit }, payload) {
    const listTicket = await getListTicketByUserIdApi(payload);
    console.log(listTicket);
    commit("setListTicketByUserMutation", listTicket);
  },
};
export default {
  namespaced: true,
  state,
  actions,
  mutations,
};
