<template>
  <div class="listing-item-container list-layout">
    <a href="listings-single-page.html" class="listing-item">
      <!-- Image -->
      <div class="listing-item-image">
        <img :src="room.image" alt="" />
        <span class="tag">Eat & Drink</span>
      </div>

      <!-- Content -->
      <div class="listing-item-content">
        <div class="listing-badge now-open">Now Open</div>

        <div class="listing-item-inner">
          <h3>{{ room.name }}</h3>
          <span>
            {{ roomDetail.locationId.name }},
            {{ roomDetail.locationId.province }}
          </span>
          <div class="star-rating" data-rating="3.5">
            <div class="rating-counter">Price: {{ room.price }}</div>
          </div>
        </div>

        <span class="like-icon"></span>
      </div>
    </a>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "vue";
export default {
  props: {
    room: {
      type: Object,
    },
  },
  setup(props) {
    const store = useStore();
    store.dispatch("rooms/getRoomDetailActions", props.room._id);
    const roomDetail = computed(() => store.state.rooms.roomDetail);
    console.log(roomDetail);
    return {
      roomDetail,
    };
  },
};
</script>

