import { signUpApi } from "@/apis/auth";
import { signInApi } from "@/apis/auth";
const state = () => {
  return {
    userRegister: {},
    userLogin: {},
  };
};
const mutations = {
  setUserRegister(state, payload) {
    state.userRegister = payload;
  },
  setUserLogin(state, payload) {
    state.userLogin = payload;
    localStorage.setItem("userLogin", JSON.stringify(payload));
  },
  loadFromLocalStorageMutations(state, payload) {
    state.userLogin = payload;
  },
  removeLocalStorageMutations(state) {
    state.userLogin = {};
  },
};
const actions = {
  async signUpActions(context, { data, router }) {
    const userRegister = await signUpApi(data);
    context.commit("setUserRegister", userRegister);
    router.push("/sign-in");
  },

  async signInActions(context, { data, router }) {
    try {
      const userLogin = await signInApi(data);
      router.push("/");
      context.commit("setUserLogin", userLogin);
    } catch (error) {
      alert("TÃ i Khoáº£n hoáº·c máº­t kháº©u khÃ´ng ÄÃºng");
    }
  },
  loadFromLocalStorageActions({ commit }) {
    let userLogin = {};
    if (localStorage.getItem("userLogin")) {
      userLogin = JSON.parse(localStorage.getItem("userLogin"));
    }
    commit("loadFromLocalStorageMutations", userLogin);
  },
  removeLocalStorageActions({ commit }) {
    if (localStorage.getItem("userLogin")) {
      localStorage.removeItem("userLogin");
    }
    commit("removeLocalStorageMutations");
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
